.home-view {
  position: relative;
  &__grid-item {
    width: 33.333333%;
    /* Landscape */
    @media only screen and (min-device-width: 320px) and (max-device-width: 480px) and (-webkit-min-device-pixel-ratio: 2) {
      width: 100%;
    }
    padding: 1rem;
    min-height: 260px;
  }
  &__profile-list {
    margin: 0px;
    padding: 0px;
    list-style: none;
    font-size: 14px;
    > li {
      padding: 2px 0px;
      > label {
        font-weight: bold;
        display: inline-block;
        min-width: 60px;
        position: relative;
        &:after {
          display: block;
          position: absolute;
          // content: '|';
          top: 0;
          right: -4px;
        }
      }
      > span {
        display: inline-block;
        padding-left: 8px;
      }
    }
  }
}
